/* src/styles/index.css */
.traffic-light {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: black;
    padding: 20px;
    border-radius: 10px;
    width: 100px;
    height: 250px;
  }
  
  .light {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    margin: 10px 0;
    background-color: gray;
    transition: background-color 0.3s, box-shadow 0.3s;
  }
  
  .selected {
    border: 2px solid white;
  }
  
  .glowing {
    box-shadow: 0 0 10px 5px rgba(255, 255, 255, 0.7);
  }
  